
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}

.btcodea {
  width: 100%;
  background: #2680d8;
  color: #FFF;
  font-size: 0.9em;
  line-height: 3em;
  text-align: center;
  position: fixed;
  bottom: 0;
  left: 50%;
  transform: translate(-50%, 0);
  -webkit-transform: translate(-50%, 0);
}
.btcodea .weixinicon {
  position: absolute;
  right: 100%;
  top:-13px;
}
#tankuang {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, .4);
}
#tankuang.hide {
  display: none;
}
.wechat .wxid {
  margin: 0;
}
#tankuang>.wechat {
  position: fixed;
  background: white;
  text-align: center;
  width: 80%;
  border: 1px solid #ccc;
  border-radius: 12px;
  box-shadow: 10px 10px 10px #ccc;
  -webkit-box-shadow: 10px 10px 10px #ccc;
  overflow: hidden;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  -webkit-transform: translate(-50%, -50%);
  padding: 0;
}
#tankuang>.wechat .p1 {
  font-size: 28px;
  font-weight: bold;
  padding: 10px;
}
#tankuang>.wechat .wxid {
  font-size: 30px;
  background: #277edb;
  color: white;
  padding: 10px;
}
#tankuang>.wechat .p2 {
  font-size: 28px;
  padding: 10px;
}
#tankuang>.wechat .p2:active {
  background: red;
}
#tankuang>.wechat .p2:active a {
  color: white;
}
.screen-name {
  position: relative;
}
.closeBtn {
  color: red;
  display: block;
  font-size: 40px;
  position: absolute;
  right: 0;
  top: 0;
  padding: 0 20px;
  border-radius: 20%;
  height: 100%;
  line-height: 1.2;
}
.closeBtn:hover {
  background: red;
  color: white;
}
#wechatBtn {
  border: 1px solid #a7a7a7;
  border-radius: 8px;
  background: white;
  padding: 8px 24px;
  position: relative;
  top: -6px;
  overflow: hidden;
}
#wechatBtn:hover {
  background: #ccc;
}
#wechatStr {
  font-size: 30px;
  color: white;
  height: 45px;
  width: 215px;
  text-align: center;
  border: none;
  display: inline-block;
  background: none;
  overflow: hidden;
}
.p1 {
  position: relative;
}
@media screen and (max-width: 321px) {
.screen-name {
    font-size: 20px;
}
}
